@media screen and (max-width: 900px)
{
  .shopSliderAndLabel {
    display: flex;
    flex-direction: column;
    /* padding: 50; */
    padding: 20px;
    width: 80%;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .shopLabel {
    text-align: left;
    white-space: nowrap;
    text-align: right;
    margin-bottom: 30px;
  }

  .shopSlider {
    width: 100%;
    /* width: 20%; */
    /* width: 300; */
    /* background-color: red; */
  }
}
@media screen and (min-width: 901px)
{
  .shopSliderAndLabel {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }
  .shopLabel {
    text-align: right;
    min-width: 200px;
    padding-right: 10px;
    /* margin-right: 40; */
    white-space: nowrap;
    text-align: right;
  }


}
